:root {
  --pc: #111111;
  --sc: #282828;
  --body-color: #484747;
  --orange-bg: #f05f25;
  --footer-bg: #414042;
  --white: #FFFFFF;
  --tf: transparent;
  --p-color: #484747;
  --h1toh6: #111111;
  --line-light: #D9D9D9;
  --btn-hover: #f05f25;
  --a-hover: #f05f25;
}

:root {
  --ani-2: all 0.2s 0s ease;
  --ani-3: all 0.3s 0s ease;
  --ani-4: all 0.4s 0s ease;
  --ani-5: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
  --font-t: 100;
  --font-el: 200;
  --font-l: 300;
  --font-r: 400;
  --font-m: 500;
  --font-sb: 600;
  --font-b: 700;
  --font-eb: 800;
  --font-black: 900;
  --font-eblack: 950;
}

body,
html {
  font-family: "Aero Matics";
  line-height: 1.5;
  font-size: 24px;
  color: var(--body-color);
}

* {
  font-family: "Aero Matics";
}

.no-scroll {
  overflow: hidden;
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  color: var(--h1toh6);
  line-height: 1.2;
}

.h1,
h1 {
  font-weight: var(--font-sb);
  font-size: 3.25rem;
}

.h2,
h2 {
  font-size: 2.375rem;
  font-weight: var(--font-m);
  color: var(--pc);
  line-height: 2.75rem;
  margin-bottom: 1.5rem;
}

.h3,
h3 {
  font-weight: var(--font-sb);
  font-size: 2rem;
}
.h3 span,
h3 span {
  color: var(--sc);
}

.h4,
h4 {
  font-weight: var(--font-sb);
  font-size: 1.75rem;
}

.h5,
h5 {
  font-weight: var(--font-m);
  font-size: 1.5rem;
}

.h6,
h6 {
  font-weight: var(--font-b);
  font-size: 1.25rem;
}

p {
  color: var(--p-color);
  font-weight: var(--font-r);
  font-size: 1.25rem;
  font-family: "Roboto", sans-serif;
}

a {
  color: var(--pc);
  text-decoration: none;
}
a:hover {
  color: var(--a-hover);
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

svg {
  width: 100%;
  height: auto;
}

.btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

body {
  background-color: #ffffff;
  overflow-x: hidden;
}

.rounded-btn {
  font-family: "Roboto", sans-serif;
  font-size: 0.875rem;
  color: var(--orange-bg);
  line-height: 1.6875rem;
  display: inline-block;
  border-radius: 1.875rem;
  min-width: 8.75rem;
  padding: 0.75rem 1.125rem;
  text-align: center;
  text-transform: capitalize;
  border: 1px solid var(--orange-bg);
  background: transparent;
}

.description p {
  display: none;
}
.description p:first-child, .description p:nth-child(2) {
  display: block;
}

b, strong {
  font-family: "Roboto", sans-serif;
}

@media (max-width: 1920px) {
  html,
body {
    font-size: 20px;
  }
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 71.8125rem;
  }
}
.h-100 {
  height: 100%;
}

main {
  min-height: 100vh;
  padding-top: 5rem;
}

.header-open {
  overflow-y: hidden;
  min-height: 0;
}

.expand-rest {
  display: none;
}

.recaptcha .wpcf7-not-valid-tip {
  position: absolute;
  font-size: 17px;
}

.pc {
  color: var(--pc);
}

.sc {
  color: var(--sc);
}

.pos-rel {
  position: relative;
}

.cursor {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
}
.cursor svg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

circle {
  fill: var(--sc);
}

.border-btn {
  display: inline-flex;
  align-items: center;
  border: 1px solid var(--pc);
  color: var(--pc);
  border-radius: 1.875rem;
  padding: 0.75rem 2.25rem;
  background: transparent;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 1.125rem;
  font-weight: var(--font-sb);
  transition: all 0.5s;
  white-space: nowrap;
}
.border-btn:hover {
  background: var(--pc);
  color: var(--white);
}
.border-btn:hover svg {
  stroke: var(--white);
  fill: var(--white);
}
.border-btn:hover svg .svg-arrow {
  transition: all 0.5s;
}

.menu-whitebtn {
  display: inline-flex;
  align-items: center;
  border: 1px solid var(--white);
  color: var(--white);
  border-radius: 1.875rem;
  padding: 0.75rem 2.25rem;
  background: transparent;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 1.125rem;
  font-weight: var(--font-sb);
  transition: all 0.5s;
  white-space: nowrap;
}
.menu-whitebtn svg .menu-line {
  stroke: var(--white);
  fill: var(--white);
}
.menu-whitebtn:hover {
  background: var(--pc);
  color: var(--white);
  border: 1px solid var(--pc);
}
.menu-whitebtn:hover svg {
  stroke: var(--white);
  fill: var(--white);
}
.menu-whitebtn:hover svg .svg-arrow {
  transition: all 0.5s;
}

.white-btn {
  display: inline-block;
  border: 1px solid var(--white);
  color: var(--white);
  border-radius: 3.125rem;
  padding: 2.28125rem 4.125rem;
  font-size: 1.375rem;
  line-height: 1.5625rem;
  cursor: pointer;
  background: transparent;
  position: relative;
  font-weight: var(--font-sb);
  z-index: 1;
  transition: all 0.5s;
  text-transform: capitalize;
  width: fit-content;
  white-space: nowrap;
}
.white-btn:hover {
  background: var(--white);
  color: var(--pc);
}

.scroll-btn {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 0.625rem 0;
  position: absolute;
  bottom: 4.6875rem;
  left: 5.125rem;
  cursor: pointer;
}
.scroll-btn p {
  font-size: 0.875rem;
  line-height: 1.875rem;
  color: var(--a-hover);
  text-transform: capitalize;
  transform: rotate(270deg);
}
.scroll-btn .scroll-arrow {
  width: 0.9375rem;
}

.mob-cls {
  display: none;
}

footer {
  position: relative;
}
footer .lets-connect {
  position: fixed;
  right: 1.875rem;
  bottom: 2.9375rem;
  z-index: 2;
}
footer .lets-connect .connect {
  max-width: 7.5rem;
  transition: 0.3s ease;
}
footer .lets-connect:hover .connect {
  scale: 1.4;
}

.for-mobile {
  display: none !important;
}

@media (max-width: 600px) {
  .cursor {
    display: none;
  }
  .for-mobile {
    display: block !important;
  }
  h2 {
    font-size: 30px;
    line-height: 40px;
  }
  .white-btn {
    padding: 16.5px 40px;
    font-size: 18px;
  }
  .border-btn {
    padding: 14.5px 28px;
    cursor: pointer;
    font-size: 14px;
  }
  .desk-cls {
    display: none;
  }
  .mob-cls {
    display: block;
  }
  footer .lets-connect {
    right: 20px;
    bottom: 30px;
  }
  footer .lets-connect .connect {
    max-width: 60px;
  }
}
.scroll-fixed {
  width: 0;
  height: 4px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: #f05f25;
  transition: all 0.5s;
}

